@import url(../App.css);

.cards {
  padding: 4rem;
  background: var(--primary);
}

h2 {
  margin-bottom: 40px;
  text-align: center;
  color: var(--dark);
  font-size: 1.8rem;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}
.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}
.cards__items {
  margin-bottom: 24px;
}
.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: .25rem;
}
.cards__item__link {
  overflow: hidden;
  display: flex;
  flex-flow: column;
  width: 100%;
  border-radius: .5rem;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
  color: var(--normal-text);
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 0.75rem;
  font-weight: 600;
  background: linear-gradient(var(--secondary) 0%, var(--bright) 100%);
  color: #fff;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.3s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 1rem 30px 1.3rem;
}

.cards__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 85%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}