@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
@charset "UTF-8";
/* Reset CSS */

html, body {
  width: 100%;
  height: 100%;
}
body, main, header, footer, div, h1, h2, h3, h4, h5, h6, p, pre, ol, dl, dt, dd, ul, li, form, fieldset, input, textarea, blockquote, table, thead, tbody, tfoot, tr, th, td, section, article, figure, figcaption, nav, aside {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  }
table {
  border-collapse: collapse;
}
caption, th {
  text-align: left;
}
address, caption, cite, code, em, th {
  font-style: normal;
  font-weight: normal;
}
ul, ol, dl {
  list-style: none;
}
img, table, button, abbr, fieldset, input, select {
  border: none;
}
img, button, input, textarea, label {
  vertical-align: middle;
}
input, button {
  -webkit-appearance: none;
          appearance: none;
  background: none;
}
button {
  cursor: pointer;
}
q:after, q::before {
  content: '';
}
a {
  text-decoration: none;
}
.skipNavi, .blind, legend, caption {
  visibility: hidden;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
}    
:root {
  --primary: #FFFBDF;
  --secondary: #65C286;
  --bright: #C6EB78;
  --dark: #32503c;
  --normal-text: #212121;
}

body {
  font-family: 'Montserrat', sans-serif;
}

.home,
.services,
.products,
.sign-up {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.services {
  background-image: url(/Website-React/static/media/img-6.cefca942.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #FFFBDF;
  color: var(--primary);
  font-size: 100px;
}

.products {
  background-image: url(/Website-React/static/media/img-3.84e98e61.jpg);
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #FFFBDF;
  color: var(--primary);
  font-size: 100px;
}

.sign-up {
  background-image: url(/Website-React/static/media/img-5.c0bb8045.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #FFFBDF;
  color: var(--primary);
  font-size: 100px;
}
/* button */
.btn {
  padding: 10px 1.5rem;
  border-radius: .25rem;
  cursor: pointer;
}
.btn--primary {
  background-color: var(--primary);
  color: var(--dark);
  border: 1px solid var(--primary);
}
.btn--outline {
  border: 1px solid var(--primary);
  background-color: transparent;
  color: var(--primary);
  transition: all .3s ease-out 0s;
}
.btn--medium {
  padding: .5rem 1.5rem;
  font-size: 1.2rem;
}
.btn--large {
  font-size: 1.5rem;
}
.btn--medium:hover, .btn--large:hover {
  background: var(--primary);
  color: var(--dark);
  transition: all .3s ease-out 0s;
}
.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky; 
  top: 0;
  height: 80px;
  background: var(--dark);
  font-size: 1.2rem;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1500px;
  height: 80px;
}

.navbar-logo {
  display: flex;
  justify-self: start;
  align-items: center;
  color: var(--primary);
  font-size: 2rem;
  font-weight: 500;
  transition: all .3s ease;
}
.navbar-logo:hover {
  opacity: .5;
}

.fa-paper-plane {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  justify-content: end;
  width: 60vw;
  grid-gap: 10px;
  grid-template-columns: repeat(4, auto);
  text-align: center;
}
.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
  padding: 0.5rem 1rem;
}

.nav-links {
  color: var(--primary);
  transition: all .3s ease-out;
}
.nav-links:hover {
  opacity: .5;
}

.fa-bars {
  color: var(--primary);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 80px;
    left: -100%;
    width: 100%;
    /*height: 90vh;*/
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: var(--dark);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 1rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    opacity: 1;
    background-color: var(--primary);
    color: var(--dark);
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: var(--primary);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: var(--primary);
    padding: 14px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: var(--primary);
    color: var(--dark);
  }
}
video {
  position: fixed;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.visual-container {
  /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
  object-fit: contain;
}

.visual-container > h1 {
  margin-top: -100px;
  color: var(--primary);
  font-size: 4rem;
}

.visual-container > p {
  margin-top: 8px;
  color: var(--primary);
  font-size: 2rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.visual-btns {
  margin-top: 32px;
}

.visual-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .visual-container > h1 {
    margin-top: -150px;
    font-size: 3.2rem;
  }
}

@media screen and (max-width: 768px) {
  .visual-container > h1 {
    margin-top: -100px;
    font-size: 2rem;
    text-align: center;
  }

  .visual-container > p {
    font-size: 1.8rem;
    text-align: center;
  }

  .btn-mobile {
    display: block;
  }

  .btn {
    width: 100%;
  }
}
.cards {
  padding: 4rem;
  background: var(--primary);
}

h2 {
  margin-bottom: 40px;
  text-align: center;
  color: var(--dark);
  font-size: 1.8rem;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}
.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}
.cards__items {
  margin-bottom: 24px;
}
.cards__item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  border-radius: .25rem;
}
.cards__item__link {
  overflow: hidden;
  display: flex;
  flex-flow: column;
  width: 100%;
  border-radius: .5rem;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
  color: var(--normal-text);
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 0.75rem;
  font-weight: 600;
  background: linear-gradient(var(--secondary) 0%, var(--bright) 100%);
  color: #fff;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.3s linear;
}

.cards__item__img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.cards__item__info {
  padding: 1rem 30px 1.3rem;
}

.cards__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 85%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}
.footer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 0 2rem;
  background: var(--dark);
}
.footer-sub {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 24px;
  padding: 24px;
  color: var(--primary);
}
.footer-sub-heading {
  margin-bottom: 24px;
  font-size: 1.8rem;
}
.footer-sub-text {
  margin-bottom: 24px;
  font-size: 1.4rem;
}
.footer-input {
  margin-right: 10px;
  padding: .5rem 20px;
  border-radius: .25rem;
  background: var(--primary);
  font-size: 1.4rem;
}

.footer-links {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1000px;
}
.footer-links-wrapper {
  display: flex;
}
.footer-link-items {
  width: 160px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 1rem;
  text-align: left;
}
.footer-link-items h3 {
  margin-bottom: 1rem;
  color: var(--primary);
}
.footer-link-items a {
  opacity: 0.6;
  margin-bottom: 0.5rem;
  color: var(--primary);
  transition: all .3s ease;
}
.footer-link-items a:hover {
  opacity: 1;
  text-decoration: underline;
}

/* sns section */
.sns {
  max-width: 1000px;
  width: 100%;
}
.sns-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1000px;
  width: 90%;
  margin: 40px auto 0;
}
.sns-icon-link {
  color: var(--primary);
  font-size: 2rem;
}
.sns-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}
.sns-logo {
  display: flex;
  align-items: center;
  color: var(--primary);
  justify-self: start;
  margin-left: 20px;
  font-size: 2rem;
}
.website-rights {
  color: var(--primary);
  margin-bottom: 1rem;
}

/* media query */
@media screen and (max-width: 960px) {
  .footer-input {
    width: 100%;
    margin-bottom: 1rem;
  }
  .btn {
    width: 100%;
  }
  .footer-links-wrapper {
    flex-direction: column;
  }
  .sns-wrap {
    flex-direction: column;
  }
}
